import React, { useState, useRef } from 'react';
import Slider from 'react-slick';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';
import { Canvas } from '@react-three/fiber';
import { OrbitControls } from '@react-three/drei';
import { Catcher } from './Catcher';

const ProductSlideshow = () => {
  const mainSlider = useRef(null);
  const navSlider = useRef(null);
  const [selectedThumbnailIndex, setSelectedThumbnailIndex] = useState(0);
  const publicUrl = '/josports/wp-content/reactpress/apps/demo/build';

  const sliderSettings = {
    slidesToShow: 1,
    slidesToScroll: 1,
    fade: true,
    arrows: true,
    asNavFor: navSlider.current,
  };

  const sliderNavSettings = {
    slidesToShow: 3,
    slidesToScroll: 1,
    dots: false,
    arrows: false,
    focusOnSelect: true,
    responsive: [
      {
        breakpoint: 767,
        settings: {
          slidesToShow: 2,
        },
      },
      {
        breakpoint: 576,
        settings: {
          slidesToShow: 1,
        },
      },
    ],
    asNavFor: mainSlider.current,
  };

  const handleThumbnailClick = (index) => {
    setSelectedThumbnailIndex(index);
    mainSlider.current.slickGoTo(index);
  };

  const render3DScene = (rotation) => (
    <Canvas
      className="m-auto"
      shadows
      style={{ width: "733px", height: "712px" }}
    >
      <ambientLight intensity={0.7} />
      <directionalLight intensity={1} position={[10, 15, 10]} />
      <pointLight intensity={1} position={[-10, 10, -10]} />
      <Catcher rotation={rotation * (Math.PI / 2)} />
    </Canvas>
  );

  const renderThumbnail = (index, rotation) => (
    <div onClick={() => handleThumbnailClick(index)}>
      <div className="product-img-box">
        <Canvas className="m-auto" shadows dpr={[1, 2]} style={{ width: "238px", height: "146px" }}>
          <ambientLight intensity={0.7} />
          <directionalLight intensity={1} position={[10, 15, 10]} />
          <pointLight intensity={1} position={[-10, 10, -10]} />
          <Catcher rotation={rotation * (Math.PI / 2)} />
        </Canvas>
      </div>
    </div>
  );

  return (
    <div className="col-lg-6 col-xl-7">
      <div className="slideshow-area">
        <Slider {...sliderSettings} className="product-slideshow" ref={mainSlider}>
          {/* Your main slideshow images */}
          {[0, 1, 2, 3].map((index) => (
            <div key={index} className="slide slick-slide slick-cloned">
              <div className="product-img-box">
                  {render3DScene(index)}
              </div>
            </div>
          ))}
        </Slider>

        <Slider {...sliderNavSettings} className="product-nav" ref={navSlider}>
          {/* Your thumbnail navigation images */}
          {[0, 1, 2, 3].map((index) => (
            <div key={index} >
              {renderThumbnail(index, index)}
            </div>
          ))}
        </Slider>
      </div>
    </div>
  );
};

export default ProductSlideshow;
